.intro__wrapper {
  width: 75%;
  margin: 0 auto;
  padding-top: 225px;
  padding-bottom: 235px;
  box-sizing: border-box;
}

.intro__title {
  width: 900px;
  height: auto;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $color-white;
}

.intro__message {
  position: relative;
  width: 723px;
  height: auto;
  margin: 0;
  margin-bottom: 45px;
  padding: 0;
  padding-left: 93px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  color: $color-white;
}

.intro__message::before {
  position: absolute;
  content: "";
  width: 66px;
  height: 2px;
  left: 0;
  top: 15px;
  background: $color-white;
}

.intro__button-order {
  margin-left: 95px;
  margin-bottom: 130px;
  padding: 17px 45px 17px 45px;
  border: 1px solid $color-lightblue;
  border-radius: 5px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  background-color: $color-lightblue;
  color: $color-white;
}

.button--short {
  display: none;
}

.intro-scroll {
  margin-left: -40px;
  width: 193px;
  min-height: 16px;
}

.intro-scroll__btn {
  transform: rotate(-90deg);
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: $color-white;
  opacity: 0.5;
  background-color: transparent;
}

@media (max-width: $desktop-width) {
  .intro {
    width: 100%;
    min-width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .intro__wrapper {
    width: 100%;
    padding-top: 110px;
    padding-left: 60px;
    padding-bottom: 115px;
  }

  .intro__title {
    width: 470px;
    height: auto;
    margin-bottom: 35px;
    font-size: 60px;
    line-height: 70px;
    letter-spacing: 0.07em;
  }

  .intro__message {
    width: 390px;
    height: auto;
    margin-bottom: 53px;
    padding-left: 50px;
    font-size: 18px;
    line-height: 156.8%;
  }

  .intro__message::before {
    position: absolute;
    content: "";
    width: 33px;
    height: 2px;
    left: 2px;
    top: 13px;
  }

  .intro__button-order {
    width: 380px;
    margin-left: 50px;
    margin-bottom: 0;
    padding: 0;
    padding-top: 17px;
    padding-bottom: 17px;
  }

  .intro-scroll {
    display: none;
  }
}

@media (max-width: $tablet-width) {
  .intro {
    width: 100%;
    min-width: 320px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .intro__wrapper {
    width: 100%;
    padding-top: 218px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 70px;
  }

  .intro__title {
    width: 277px;
    height: auto;
    margin-bottom: 30px;
    font-size: 40px;
    line-height: 47px;
  }

  .intro__message {
    width: 100%;
    height: auto;
    margin-bottom: 40px;
    font-size: 16px;
  }

  .intro__button-order {
    width: 100%;
    margin: 0 auto;
  }

  .button--short {
    display: block;
  }

  .button--long {
    display: none;
  }
}
