.popup-form__textarea {
  width: 100%;
  height: 104px;
  margin-bottom: 43px;
  padding-top: 20px;
  padding-left: 20px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  box-sizing: border-box; }

.popup-form__form-button {
  width: 100%;
  height: auto;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #ffffff;
  border: 1px solid #ffffff; }

.popup-form__form-checkbox {
  width: 100%;
  height: auto;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px; }

.popup-form__checkbox-label {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  height: auto;
  padding-left: 40px;
  background-image: url("../img/checkbox-rec.png");
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: 0 2px;
  cursor: pointer; }

.popup-form__checkbox-input:checked ~ .popup-form__checkbox-label[for="agreement"] {
  background-image: url("../img/checkbox-tic.png"), url("../img/checkbox-rec.svg");
  background-repeat: no-repeat, no-repeat;
  background-size: 10px 10px, 16px 16px;
  background-position: 3px 4px, 0 2px; }

.popup-form,
.modal-layout {
  display: none; }

@media (max-width: 1023px) {
  .popup-form,
  .modal-layout {
    display: none; }
  .popup-form__wrapper {
    width: 590px; }
  .popup-form__textarea {
    width: 590px; }
  .popup-form__input {
    width: 280px; } }

.popup-form__button-wrapper {
  width: 20px;
  height: 20px;
  margin: 0;
  padding: 0;
  margin-left: auto; }

.popup-form__button-close {
  padding: 0;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer; }
